import React, { Component } from 'react';
import Input from '../common/Input';
import Fetch from '../common/Fetch';
import Popups from './Popups';

class JoinNowInput extends Component {

    state = {
        phone: '',
        phoneError: 1,
        select: '+91',
        countries: {},
        startFetch: null,
        showPopup: 0,
        successMessage: { type: 0, msg: '' },
    }

    handleChange = e => {
        const { name, value } = e.target;

        this.setState({ [name]: value,})

        if (!e.isValid) {
            this.setState({ startFetch: 0, phoneError: 1 })
        } else {
            this.setState({ startFetch: 1, phoneError: 0 })
        }

    }

    handleSubmit = (e) => {
        e.preventDefault();
        if (this.state.startFetch) {
            this.setState({ showPopup: 1 })

            Fetch("preregister", { phone: this.state.select + this.state.phone }).then(data => {
                if(data.status){
                    this.setState({ successMessage: { type: 1, msg: data.msg.message }, phone: '' })
                    // console.log(data);
                }
                
            }).catch(e =>{
                this.setState({ successMessage: { type: 3, msg: "Please try again later" } }, console.log(e))
                setTimeout(()=>{
                    this.setState({successMessage: {type: 3, msg:''}, phone: ''})
                }, 10000)

            })
        }
    }

    onPopupClose = () => {
        this.setState({ showPopup: 0 })
    }

    render() {
        const {phone, phoneError,successMessage } = this.state

        return (
            <div className="subscription">
                <div className="subscription-input">
                    <select className="countryCode"
                        name="select" id="select"
                        onChange={this.handleChange}>
                        <option key="0" value="+91">+91</option>
                        {
                            this.props.countryCode && this.props.countryCode.length ?
                                this.props.countryCode.map((code, i) => (
                                    <option key={i + 1} value={code.dial_code}>{code.dial_code}</option>
                                )) : null
                        }
                    </select>

                    <Input
                        type="tel"
                        id="phoneNumber"
                        validFor="mobileNumber"
                        validClassName="valid"
                        invalidClassName="invalid"
                        placeholder="Phone number"
                        value={phone}  
                        name="phone"
                        onChange={this.handleChange}
                    />
                </div>

                <input type="submit" value="JOIN NOW" className="d-sm-done"
                    style={{ cursor: phone === '' || phoneError ? "not-allowed" : ""}}
                    disabled={phone === '' || phoneError ? true : false}
                    onClick={this.handleSubmit} />
                    {
                    this.state.showPopup === 1 && successMessage.type === 1
                        ?
                        <Popups
                            successMSg={this.state.successMessage}
                            type={this.state.successMessage.type}
                            msg={this.state.successMessage.msg}
                            onClose={this.onPopupClose}
                        /> : null
                    }
            </div>
        );
    }
}

export default JoinNowInput;


